import React, { Component } from 'react';
import { connect } from 'react-redux';
import { saveFriendAnswers, showLoader, saveFriendKey, saveAuthorName } from '../../store/actions_creator';
import Button from '../../elements/button';
import Input from '../../elements/Input';
import "./welcome.css";

class Welcome extends Component {
    state = {
        inputText: ''
    }

    componentDidMount() {
        const { pathname } = this.props.location;
        console.log(this.props);
    }
    handleOnclick = () => {
        const { match: { params, path }, saveFriendAnswers, history, showLoader, saveFriendKey, saveAuthorName } = this.props;
        showLoader(true);
        console.log(this.state.inputText);
        saveAuthorName(this.state.inputText);
        if (params.id && path === "/quiz/:id") {
            saveFriendAnswers(params.id).then((isFriend) => {
                if (isFriend) {
                    saveFriendKey(params.id);
                    history.push("/question");
                    showLoader(false);
                }
            });
        } else {
            if (path === "/") {
                history.push("/question");
                showLoader(false);
            }
        }

    }
    handleInputTextChange = (e) => {
        console.log(e.target.value);
        e.persist();
        this.setState((previousState) => ({
            ...previousState,
            inputText: e.target.value
        }))
    }
    render() {
        return (
            <div className="welcome-cmp">
                <div className="container v-center">
                    <div className="box v-center">
                        <div className="field ">
                            <div className="control">
                                <Input value={this.state.inputText} onInputChange={this.handleInputTextChange} />
                            </div>
                            <div className="btn-add">
                                <Button disabled={this.state.inputText.length ? false : true} size="is-small" color="is-primary" onClick={this.handleOnclick}>Start</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = ({ isFriend }) => ({
    isFriend
})

export default connect(mapStateToProps, { saveFriendAnswers, showLoader, saveFriendKey, saveAuthorName })(Welcome)